* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* CSS for Navbar.js Start */

.navbar_container {
  margin: 0%;
  background-color: #4361ee;
  display: flex;
  align-items: center;
  padding: 15px 10px;
  justify-content: space-between;
  width: 100%;
}

.navbar_container .logo {
  color: #fff;
  width: 20%;


}

.navbar_container .logo h1 {
  font-weight: 400;
  position: relative;
  cursor: pointer;
}

.navbar_container .logo h1 span {
  font-weight: bold;
  position: absolute;
  top: 15px;
  left: 90px;
}

.navbar_container .menu {
  display: flex;
  align-items: center;
  justify-content: start;
  width: 50%;
  padding-left: 30px;
}

.navbar_container .menu h3 {
  color: white;
  margin-right: 25px;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}

/* .navbar_container .menu .selected{
    color: #ffd500;
    text-decoration: underline;
    text-decoration-color:#ffd500 ;
} */
.navbar_container .menu h3:hover {
  color: #fdc500;
  transform: scale(1.1);
}

.navbar_container .search {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 30%;
  cursor: pointer;
  padding-right: 20px;
}

.navbar_container .search input {
  color: #4361ee;
  width: 75%;
  border: none;
  outline: none;
  height: 40px;
  padding: 5px 0px 5px 10px;
  font-size: 20px;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.navbar_container .search .MuiSvgIcon-root {
  background-color: #fdc500;
  color: #4361ee;
  padding: 1px;
  height: 40px;
  width: 40px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.navbar_container .search .MuiSvgIcon-root:hover {
  background-color: #ffd500;
}

/* CSS for Navbar.js END */



/* CSS for NewsBody.js Start */

.newsbody_container_parent {
  display: flex;
  flex-wrap: wrap;
}

.newsbody_container {
  margin: 20px 10px;
  width: 300px;
  height: 350px;
  display: block;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  border-radius: 10px;
  cursor: pointer;
}

.newsbody_container:hover {
  transform: scale(1.01);
}

.newsbody_container img {
  width: 100%;
  height: 200px;
  border-top-right-radius: 10px;
  border-top-left-radius: 10px;
}

.newsbody_container .article_description {
  width: 100%;
  height: 150px;
  padding: 10px;

}

.newsbody_container .article_description p {
  font-size: 20px;
}

.newsbody_container .article_description p span {
  color: red;
}

.newsbody_container .article_description p span:hover {
  color: blue;
}

/* CSS for NewsBody.js END */